import axios from "axios";

/* =========================
    Base URL (Vite)
========================= */
const API_URL = import.meta.env.VITE_API_URL;

/* =========================
    Axios Instance
========================= */
const apiClient = axios.create({
  baseURL: API_URL,
  headers: {
    "Content-Type": "application/json",
  },
});

/* =========================
    Interceptor
========================= */
apiClient.interceptors.request.use((config) => {
  const userStr = localStorage.getItem("user");
  if (userStr) {
    try {
      const user = JSON.parse(userStr);
      if (user.token) {
        config.headers.Authorization = `Bearer ${user.token}`;
      }
      if (user.role) {
        config.headers["x-user-role"] = user.role;
      }
    } catch {}
  }
  return config;
});

/* =========================
    API Facade ( )
========================= */
const api = {
  get: apiClient.get,
  post: apiClient.post,
  put: apiClient.put,
  delete: apiClient.delete,

  orders: {
    getOrders: (params?: any) =>
      apiClient.get("/orders", { params }).then(res => res.data),

    getOrderDetails: (id: number) =>
      apiClient.get(`/orders/${id}`).then(res => res.data),

    assignCaptain: (orderId: number, captainId: number) =>
      apiClient.post(`/orders/${orderId}/assign-captain`, { captain_id: captainId }),

    updateStatus: (orderId: number, status: string) =>
      apiClient.put(`/orders/${orderId}/status`, { status }),
  },

  captains: {
    getAvailableCaptains: () =>
      apiClient.get("/captains").then(res => res.data),
  },
};



/* ======================================================
    Interceptor:      
====================================================== */
apiClient.interceptors.request.use((config) => {
  const userStr = localStorage.getItem("user");

  if (userStr) {
    try {
      const user = JSON.parse(userStr);

      if (user.token) {
        config.headers.Authorization = `Bearer ${user.token}`;
      }

      if (user.role) {
        config.headers["x-user-role"] = user.role;
      }
    } catch {
      console.warn("Failed to parse user from localStorage");
    }
  }

  return config;
});

/* ======================================================
    Interceptor: 
====================================================== */
apiClient.interceptors.response.use(
  (response) => response,
  (error) => {
    console.error(" API Error:", error.response?.data || error.message);
    return Promise.reject(error);
  }
);

