import React, { useEffect, useState } from "react";
import api from "../services/api";

/* =========================
   Types
========================= */
interface City {
  id: number;
  name: string;
  delivery_fee: number;
  neighborhoods: number; //  
}

/* =========================
   Component
========================= */
const Cities: React.FC = () => {
  const [cities, setCities] = useState<City[]>([]);
  const [loading, setLoading] = useState<boolean>(true);

  const [name, setName] = useState<string>("");
  const [deliveryFee, setDeliveryFee] = useState<number>(0);

  /* =========================
     Fetch Cities
  ========================= */
  const fetchCities = async () => {
    setLoading(true);
    try {
      const res = await api.cities.getCities();

      //    
      setCities(Array.isArray(res.cities) ? res.cities : []);
    } catch (err) {
      console.error("    :", err);
      setCities([]);
    } finally {
      setLoading(false);
    } 
  };

 /* =========================
   Add City
========================= */
const addCity = async (e: React.FormEvent) => {
  e.preventDefault();

  if (!name || deliveryFee < 0) return;

  try {
    await api.cities.addCity(name, deliveryFee);

    setName("");
    setDeliveryFee(0);
    fetchCities();
  } catch (err) {
    console.error("    :", err);
  }
};

  /* =========================
     Delete City
  ========================= */
  const deleteCity = async (id: number) => {
    if (!window.confirm("    ")) return;

    try {
      await api.cities.deleteCity(id);
      fetchCities();
    } catch (err) {
      console.error("    :", err);
    }
  };

  /* =========================
     Helpers
  ========================= */
  const renderNeighborhoods = (value: unknown) => {
    if (typeof value === "number") return value;
    if (typeof value === "string" && !isNaN(Number(value)))
      return Number(value);
    if (Array.isArray(value)) return value.length;
    return 0;
  };

  /* =========================
     Lifecycle
  ========================= */
  useEffect(() => {
    fetchCities();
  }, []);

  /* =========================
     Render
  ========================= */
  return (
    <div className="p-6">
      <h1 className="text-2xl font-bold mb-6"> </h1>

      {/*   */}
      <form onSubmit={addCity} className="flex gap-2 mb-6">
        <input
          type="text"
          placeholder=" "
          value={name}
          onChange={(e) => setName(e.target.value)}
          className="border px-3 py-2 rounded w-1/3"
          required
        />

        <input
          type="number"
          placeholder=" "
          value={deliveryFee}
          onChange={(e) => setDeliveryFee(Number(e.target.value))}
          className="border px-3 py-2 rounded w-1/3"
          required
        />

        <button
          type="submit"
          className="bg-green-600 text-white px-4 py-2 rounded"
        >
          
        </button>
      </form>

      {/*  */}
      {loading ? (
        <p> ...</p>
      ) : (
        <table className="w-full border">
          <thead>
            <tr className="bg-gray-200">
              <th className="px-4 py-2">#</th>
              <th></th>
              <th> </th>
              <th> </th>
              <th></th>
            </tr>
          </thead>

          <tbody>
            {cities.length === 0 ? (
              <tr>
                <td colSpan={5} className="text-center py-4">
                    
                </td>
              </tr>
            ) : (
              cities.map((city, index) => (
                <tr key={city.id} className="border-b">
                  <td className="px-4 py-2">{index + 1}</td>
                  <td>{city.name}</td>
                  <td>{city.delivery_fee}</td>
                  <td>{renderNeighborhoods(city.neighborhoods)}</td>
                  <td>
                    <button
                      onClick={() => deleteCity(city.id)}
                      className="text-red-600 hover:underline"
                    >
                      
                    </button>
                  </td>
                </tr>
              ))
            )}
          </tbody>
        </table>
      )}
    </div>
  );
};

export default Cities;
