import React, { useEffect, useState } from "react";
import api from "../services/api";

interface User {
  id: number;
  name: string;
  email?: string;
  phone?: string;
  role: string;
  permissions?: any;
  status: string;
  image_url?: string;
}

interface Section {
  key: string;
  label: string;
}

const Users: React.FC = () => {
  const [users, setUsers] = useState<User[]>([]);
  const [sections, setSections] = useState<Section[]>([]);
  const [loading, setLoading] = useState(true);
  const [isModalOpen, setIsModalOpen] = useState(false);
  const [editingUser, setEditingUser] = useState<User | null>(null);

  //  
  const [name, setName] = useState("");
  const [username, setUsername] = useState("");
  const [password, setPassword] = useState("");
  const [confirmPassword, setConfirmPassword] = useState("");
  const [image, setImage] = useState<File | null>(null);
  const [role, setRole] = useState("section");

  //   (JSON )
  const [permissions, setPermissions] = useState<any>({});

  //  
  const fetchUsers = async () => {
    setLoading(true);
    try {
      const data = await api.users.getUsers();
      setUsers(data);
    } catch (err) {
      console.error("    :", err);
    } finally {
      setLoading(false);
    }
  };

  //  
 const fetchSections = async () => {
  try {
    //    api.sections 
    if (!api.sections || !api.sections.getSections) {
      setSections([]);
      return;
    }

    const data = await api.sections.getSections();
    setSections(data || []);
  } catch (err) {
    console.error("    :", err);
    setSections([]); //    
  }
}; 

  useEffect(() => {
    fetchUsers();
    fetchSections();
  }, []);

  //    
  const togglePermission = (sectionKey: string, action: string) => {
    setPermissions((prev: any) => {
      const updated = { ...prev };
      if (!updated[sectionKey]) updated[sectionKey] = {};
      updated[sectionKey][action] = !updated[sectionKey][action];
      return updated;
    });
  };
    //   
  const openEditModal = (user: User) => {
    setEditingUser(user);
    setName(user.name);
    setUsername(user.email || user.phone || "");
    setRole(user.role);

    //   
    setPermissions(user.permissions || {});

    //   
    setPassword("");
    setConfirmPassword("");
    setImage(null);

    setIsModalOpen(true);
  };

  //   
  const openAddModal = () => {
    setEditingUser(null);
    setName("");
    setUsername("");
    setPassword("");
    setConfirmPassword("");
    setImage(null);
    setRole("section");
    setPermissions({});
    setIsModalOpen(true);
  };

  //  /
  const handleSaveUser = async (e: React.FormEvent) => {
    e.preventDefault();

    if (!editingUser && password !== confirmPassword) {
      alert("    ");
      return;
    }

    try {
      const formData = new FormData();
      formData.append("name", name);
      formData.append("username", username);

      if (password) formData.append("password", password);
      if (image) formData.append("image", image);

      formData.append("role", role);
      formData.append("permissions", JSON.stringify(permissions));

      if (editingUser) {
        await api.users.updateUser(editingUser.id, formData);
        alert("   ");
      } else {
        await api.users.addUser(formData);
        alert("   ");
      }

      setIsModalOpen(false);
      fetchUsers();
    } catch (error) {
      console.error(":", error);
      alert("     ");
    }
  };

  //  
  const deleteUser = async (id: number) => {
    if (!window.confirm("    ")) return;
    await api.users.deleteUser(id);
    fetchUsers();
  };

  //  
  const disableUser = async (id: number) => {
    if (!window.confirm("    ")) return;
    await api.users.disableUser(id);
    fetchUsers();
  };

  //     ( + )
  const resetUserPassword = async (id: number) => {
    if (!window.confirm("      ")) return;

    const res = await api.users.resetPassword(id);

    if (res.success) {
      const pass = res.new_password;

      //    
      navigator.clipboard.writeText(pass);

      alert(`   : ${pass}\n     `);
    } else {
      alert("     ");
    }
  };

  //   
  const getDisplayName = (user: User) =>
    user.name || user.email || user.phone || " ";
    return (
    <div className="space-y-6">
      {/* =========   ========== */}
      <div className="flex justify-between items-center">
        <h1 className="text-2xl font-bold"> </h1>

        <button
          onClick={openAddModal}
          className="bg-green-600 text-white px-4 py-2 rounded hover:bg-green-700"
        >
           
        </button>
      </div>

      {/* =========   ========== */}
      <div className="bg-white rounded-xl shadow overflow-hidden">
        {loading ? (
          <div className="p-6 text-center text-gray-600"> ...</div>
        ) : users.length === 0 ? (
          <div className="p-6 text-center text-gray-600">  </div>
        ) : (
          <table className="w-full text-right">
            <thead className="bg-gray-100">
              <tr>
                <th className="px-4 py-3">#</th>
                <th className="px-4 py-3"></th>
                <th className="px-4 py-3"></th>
                <th className="px-4 py-3"></th>
                <th className="px-4 py-3 text-center"></th>
              </tr>
            </thead>

            <tbody className="divide-y divide-gray-200">
              {users.map((u, index) => (
                <tr key={u.id} className="hover:bg-gray-50">
                  <td className="px-4 py-2">{index + 1}</td>
                  <td className="px-4 py-2">{getDisplayName(u)}</td>
                  <td className="px-4 py-2">
                    {u.role === "admin" ? "" : ""}
                  </td>
                  <td className="px-4 py-2">
                    {u.status === "active" ? (
                      <span className="text-green-600 font-semibold"></span>
                    ) : (
                      <span className="text-red-600 font-semibold"></span>
                    )}
                  </td>

                  <td className="px-4 py-2 text-center flex justify-center gap-3">
                    <button
                      onClick={() => openEditModal(u)}
                      className="text-blue-600 hover:underline"
                    >
                      
                    </button>

                    <button
                      onClick={() => resetUserPassword(u.id)}
                      className="text-purple-600 hover:underline"
                    >
                        
                    </button>

                    <button
                      onClick={() => disableUser(u.id)}
                      className="text-yellow-600 hover:underline"
                    >
                      
                    </button>

                    <button
                      onClick={() => deleteUser(u.id)}
                      className="text-red-600 hover:underline"
                    >
                      
                    </button>
                  </td>
                </tr>
              ))}
            </tbody>
          </table>
        )}
      </div>

      {/* =========    ========== */}
      {isModalOpen && (
        <div className="fixed inset-0 bg-black/50 flex justify-center items-center z-50">
          <div className="bg-white w-full max-w-lg p-6 rounded-lg shadow-xl max-h-[90vh] overflow-auto">

            <h2 className="text-xl font-bold mb-4">
              {editingUser ? " " : " "}
            </h2>

            <form onSubmit={handleSaveUser} className="space-y-4">

              {/*  */}
              <input
                type="text"
                className="border p-2 rounded w-full"
                placeholder=" "
                value={name}
                onChange={(e) => setName(e.target.value)}
                required
              />

              {/*    */}
              <input
                type="text"
                className="border p-2 rounded w-full"
                placeholder="   "
                value={username}
                onChange={(e) => setUsername(e.target.value)}
                required
              />

              {/*       */}
              {!editingUser && (
                <>
                  <input
                    type="password"
                    className="border p-2 rounded w-full"
                    placeholder=" "
                    value={password}
                    onChange={(e) => setPassword(e.target.value)}
                    required
                  />

                  <input
                    type="password"
                    className="border p-2 rounded w-full"
                    placeholder="  "
                    value={confirmPassword}
                    onChange={(e) => setConfirmPassword(e.target.value)}
                    required
                  />
                </>
              )}

              {/*   */}
              <input
                type="file"
                accept="image/*"
                onChange={(e) => setImage(e.target.files?.[0] || null)}
              />

              {/*  */}
              <select
                className="border p-2 rounded w-full"
                value={role}
                onChange={(e) => setRole(e.target.value)}
              >
                <option value="admin"></option>
                <option value="section"> </option>
              </select>

              {/* =========   ========== */}
              {role === "section" && (
                <div className="border rounded p-3 space-y-4">
                  <h3 className="font-bold mb-2"> </h3>

                  {sections.map((sec) => (
                    <div key={sec.key} className="border rounded p-2">

                      <h4 className="font-semibold mb-2">{sec.label}</h4>

                      <div className="flex flex-wrap gap-4">

                        <label>
                          <input
                            type="checkbox"
                            checked={permissions[sec.key]?.view || false}
                            onChange={() => togglePermission(sec.key, "view")}
                          /> 
                        </label>

                        <label>
                          <input
                            type="checkbox"
                            checked={permissions[sec.key]?.add || false}
                            onChange={() => togglePermission(sec.key, "add")}
                          /> 
                        </label>

                        <label>
                          <input
                            type="checkbox"
                            checked={permissions[sec.key]?.edit || false}
                            onChange={() => togglePermission(sec.key, "edit")}
                          /> 
                        </label>

                        <label>
                          <input
                            type="checkbox"
                            checked={permissions[sec.key]?.delete || false}
                            onChange={() => togglePermission(sec.key, "delete")}
                          /> 
                        </label>

                        <label>
                          <input
                            type="checkbox"
                            checked={permissions[sec.key]?.print || false}
                            onChange={() => togglePermission(sec.key, "print")}
                          /> 
                        </label>

                      </div>
                    </div>
                  ))}

                </div>
              )}

              {/*  */}
              <div className="flex justify-end gap-2 mt-4">

                <button
                  type="submit"
                  className="bg-green-600 text-white px-4 py-2 rounded hover:bg-green-700"
                >
                  
                </button>

                <button
                  type="button"
                  onClick={() => setIsModalOpen(false)}
                  className="bg-gray-400 text-white px-4 py-2 rounded hover:bg-gray-500"
                >
                  
                </button>

              </div>

            </form>
          </div>
        </div>
      )}

    </div>
  );
};

export default Users;