import React, { useEffect, useState } from "react";
import api from "../services/api";

interface AgentGroup {
  id: number;
  name: string;
  code: string;
  status?: string;
}

const AgentGroups: React.FC = () => {
  const [groups, setGroups] = useState<AgentGroup[]>([]);
  const [filteredGroups, setFilteredGroups] = useState<AgentGroup[]>([]);

  const [name, setName] = useState("");
  const [code, setCode] = useState("");
  const [search, setSearch] = useState("");

  const [loading, setLoading] = useState(true);
  const [isModalOpen, setIsModalOpen] = useState(false);

  /* =========================
      
  ========================= */
  const fetchGroups = async () => {
    setLoading(true);
    try {
      const data = await api.agentGroups.getGroups();
      setGroups(data);
      setFilteredGroups(data);
    } catch (err) {
      console.error("   ", err);
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    fetchGroups();
  }, []);

  /* =========================
     
  ========================= */
  useEffect(() => {
    const q = search.toLowerCase();
    setFilteredGroups(
      groups.filter(
        (g) =>
          g.name.toLowerCase().includes(q) ||
          g.code.toLowerCase().includes(q)
      )
    );
  }, [search, groups]);

  /* =========================
      
  ========================= */
  const handleAdd = async () => {
    if (!name || !code) {
      alert("  ");
      return;
    }

    try {
      await api.agentGroups.addGroup({ name, code });
      setName("");
      setCode("");
      setIsModalOpen(false);
      fetchGroups();
    } catch (err) {
      alert("   ");
    }
  };

  /* =========================
      
  ========================= */
  const handleDelete = async (id: number) => {
    if (!window.confirm("   ")) return;

    await api.agentGroups.deleteGroup(id);
    fetchGroups();
  };

  /* =========================
     Render
  ========================= */
  return (
    <div className="space-y-6">

      {/*  +   */}
      <div className="flex justify-between items-center">
        <h1 className="text-2xl font-bold"> </h1>

        <button
          onClick={() => setIsModalOpen(true)}
          className="bg-green-600 text-white px-4 py-2 rounded hover:bg-green-700"
        >
           
        </button>
      </div>

      {/*  */}
      <input
        type="text"
        placeholder="   ..."
        className="border p-2 rounded w-full"
        value={search}
        onChange={(e) => setSearch(e.target.value)}
      />

      {/*   */}
      <div className="bg-white rounded-lg shadow overflow-hidden">
        {loading ? (
          <div className="p-4 text-center"> ...</div>
        ) : filteredGroups.length === 0 ? (
          <div className="p-4 text-center text-gray-500">
              
          </div>
        ) : (
          <table className="w-full text-right">
            <thead className="bg-gray-100">
              <tr>
                <th className="p-3">#</th>
                <th className="p-3"></th>
                <th className="p-3"></th>
                <th className="p-3 text-center"></th>
              </tr>
            </thead>

            <tbody>
              {filteredGroups.map((g, i) => (
                <tr key={g.id} className="border-t hover:bg-gray-50">
                  <td className="p-3">{i + 1}</td>
                  <td className="p-3">{g.name}</td>
                  <td className="p-3">{g.code}</td>
                  <td className="p-3 text-center">
                    <button
                      onClick={() => handleDelete(g.id)}
                      className="text-red-600 hover:underline"
                    >
                      
                    </button>
                  </td>
                </tr>
              ))}
            </tbody>
          </table>
        )}
      </div>

      {/* =========================
           (Modal)
      ========================= */}
      {isModalOpen && (
        <div className="fixed inset-0 bg-black/40 flex items-center justify-center z-50">
          <div className="bg-white w-full max-w-md rounded-lg p-6 shadow-lg">

            <h2 className="text-xl font-bold mb-4">
                
            </h2>

            <div className="space-y-4">
              <input
                type="text"
                placeholder=" "
                className="border p-2 rounded w-full"
                value={name}
                onChange={(e) => setName(e.target.value)}
              />

              <input
                type="text"
                placeholder=" "
                className="border p-2 rounded w-full"
                value={code}
                onChange={(e) => setCode(e.target.value)}
              />
            </div>

            <div className="flex justify-end gap-2 mt-6">
              <button
                onClick={() => setIsModalOpen(false)}
                className="bg-gray-400 text-white px-4 py-2 rounded"
              >
                
              </button>

              <button
                onClick={handleAdd}
                className="bg-green-600 text-white px-4 py-2 rounded hover:bg-green-700"
              >
                
              </button>
            </div>

          </div>
        </div>
      )}

    </div>
  );
};

export default AgentGroups;
