import React, { useState, useEffect } from "react";
import { useNavigate } from "react-router-dom";

const Login: React.FC = () => {
  const navigate = useNavigate();
  const [identifier, setIdentifier] = useState("");
  const [password, setPassword] = useState("");
  const [error, setError] = useState("");
  const [loading, setLoading] = useState(false);

  //          
  useEffect(() => {
    const savedUser = localStorage.getItem("user");
    if (savedUser) navigate("/");
  }, [navigate]);

  //     
  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    setError("");
    setLoading(true);

    try {
      const res = await fetch("http://localhost:5000/login", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({ identifier, password }),
      });

      const data = await res.json();
      console.log("Login response:", data);

      if (!data.success) {
        setError(data.message || "    ");
      } else {
        //         
        localStorage.setItem("user", JSON.stringify(data.user));

        //       
        window.dispatchEvent(new Event("storage"));

        alert("    ");
        navigate("/", { replace: true });
      }
    } catch (err) {
      console.error(" :", err);
      setError("   ");
    } finally {
      setLoading(false);
    }
  };

  return (
    <div className="flex items-center justify-center min-h-screen bg-gray-100">
      <form
        onSubmit={handleSubmit}
        className="bg-white shadow-lg rounded-lg p-6 w-full max-w-md"
      >
        <h1 className="text-2xl font-bold text-center mb-6"> </h1>

        {error && (
          <div className="bg-red-100 text-red-700 p-2 rounded mb-4 text-center">
            {error}
          </div>
        )}

        <div className="mb-4">
          <label className="block mb-1 text-right font-medium">
                
          </label>
          <input
            type="text"
            value={identifier}
            onChange={(e) => setIdentifier(e.target.value)}
            className="w-full border rounded px-3 py-2 text-right focus:ring focus:ring-blue-200"
            placeholder="example@mail.com  05XXXXXXXX"
            required
          />
        </div>

        <div className="mb-6">
          <label className="block mb-1 text-right font-medium"> </label>
          <input
            type="password"
            value={password}
            onChange={(e) => setPassword(e.target.value)}
            className="w-full border rounded px-3 py-2 text-right focus:ring focus:ring-blue-200"
            placeholder="  "
            required
          />
        </div>

        <button
          type="submit"
          disabled={loading}
          className="w-full bg-blue-600 text-white py-2 rounded hover:bg-blue-700 transition"
        >
          {loading ? " ..." : " "}
        </button>

        <p className="text-center text-sm text-gray-500 mt-3">
           {new Date().getFullYear()}   
        </p>
      </form>
    </div>
  );
};

export default Login;